import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Optional,
  Output,
  Self,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import {
  AbstractControl,
  FormGroupDirective,
  FormsModule,
  NgControl,
  NgForm,
  ValidationErrors,
  ValidatorFn
} from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MAT_FORM_FIELD_DEFAULT_OPTIONS, MatFormFieldModule } from '@angular/material/form-field';
import { NgClass, NgIf } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { DateFnsAdapter } from '@angular/material-date-fns-adapter';
import { MatInput, MatInputModule } from '@angular/material/input';
import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { A11yModule } from '@angular/cdk/a11y';

import { ptBR } from 'date-fns/locale';

// @ts-ignore
import Inputmask from 'inputmask/dist/inputmask.es6.js';

import { BaseComponent } from 'sidsc-components/core';
import { DscTooltipModule } from 'sidsc-components/dsc-tooltip';

export const DEFAULT_DATE_FORMATS = {
  parse: { dateInput: 'dd/MM/yyyy' },
  display: {
    dateInput: 'dd/MM/yyyy',
    monthYearLabel: 'MMM yyyy',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM yyyy',
  },
};

export const MONTH_YEAR_DATE_FORMATS = {
  parse: { dateInput: 'MM/yyyy' },
  display: {
    dateInput: 'MM/yyyy',
    monthYearLabel: 'MMM yyyy',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM yyyy',
  },
};

export function getDateFormats(component: DscDatepickerComponent) {
  return component.monthYear ? MONTH_YEAR_DATE_FORMATS : DEFAULT_DATE_FORMATS;
}

@Component({
  selector: 'dsc-datepicker',
  standalone: true,
  encapsulation: ViewEncapsulation.None,
  imports: [
    FormsModule,
    MatFormFieldModule,
    MatButtonModule,
    MatIconModule,
    MatTooltipModule,
    DscTooltipModule,
    MatInputModule,
    NgIf,
    NgClass,
    MatDatepickerModule,
    A11yModule
  ],
  templateUrl: './dsc-datepicker.component.html',
  styleUrls: ['./dsc-datepicker.component.scss'],
  providers: [
    { provide: MAT_DATE_LOCALE, useValue: ptBR },
    { provide: DateAdapter, useClass: DateFnsAdapter, deps: [MAT_DATE_LOCALE] },
    {
      provide: MAT_DATE_FORMATS,
      useFactory: getDateFormats,
      deps: [DscDatepickerComponent],
    },
    { provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: { appearance: 'outline' } }
  ]
})
export class DscDatepickerComponent extends BaseComponent<Date> implements OnInit, AfterViewInit {
  @ViewChild(MatInput, { static: true, read: ElementRef })
  dateInputElement!: ElementRef;

  @Input()
  maxDate!: Date | null;

  @Input()
  minDate!: Date | null;

  @ViewChild('picker') picker!: any;

  private _monthYear = false;

  @Input()
  get monthYear(): boolean {
    return this._monthYear;
  }

  set monthYear(value: boolean) {
    this._monthYear = coerceBooleanProperty(value);
  }

  @Input()
  datepickerFilter!: (date: Date | null) => boolean;

  @Input()
  get touchUi(): boolean {
    return this._touchUi;
  }

  set touchUi(value: boolean | string) {
    this._touchUi = coerceBooleanProperty(value);
  }

  private _touchUi = false;

  isActive: boolean = false;

  @Output()
  dateChange: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  dateInput: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  opened: EventEmitter<any> = new EventEmitter<any>();

  @Output()
  closed: EventEmitter<any> = new EventEmitter<any>();

  constructor(@Optional() @Self() public ngControl: NgControl,
              @Optional() parentForm: NgForm,
              @Optional() parentFormGroup: FormGroupDirective,
              @Optional() private _dateAdapter: DateAdapter<Date>
  ) {
    super(ngControl, parentForm, parentFormGroup);
    if (this.ngControl) this.ngControl.valueAccessor = this;
    if (!this._dateAdapter) throw Error('MatDatepicker: No provider found for DateAdapter.',);
  }

  ngOnInit(): void {
    this._initValidators();
  }

  private _initValidators(): void {
    if (this.ngControl) {
      const control = this.ngControl.control;
      control?.addValidators(this._getValidators());
      control?.updateValueAndValidity();
    }
  }

  ngAfterViewInit(): void {
    this._maskInit();
  }

  private _maskInit(): void {
    if (this.dateInputElement && this.dateInputElement.nativeElement) {
      Inputmask.remove(this.dateInputElement.nativeElement);

      const options = this.monthYear
        ? {
          alias: 'datetime',
          inputFormat: 'mm/yyyy',
          placeholder: 'MM/AAAA',
          clearIncomplete: true
        }
        : {
          alias: 'datetime',
          inputFormat: 'dd/mm/yyyy',
          placeholder: 'DD/MM/AAAA',
          clearIncomplete: true
        };

      Inputmask(options).mask(this.dateInputElement.nativeElement);
    }
  }

  onDateSelection(date: Date): void {
    if (this.monthYear) {
      this.dateInputElement.nativeElement.value = this._formatMonthYear(date);
      this.dateChange.emit(date);
      this.picker.close();
    }
  }

  private _formatMonthYear(date: Date): string {
    const month = date.toLocaleDateString('pt-BR', { month: '2-digit' });
    const year = date.getFullYear();
    return `${month}/${year}`;
  }

  private _getValidators(): ValidatorFn[] {
    return [this._minValidator, this._maxValidator, this._filterValidator];
  }

  private _filterValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
    const controlValue = this._dateAdapter.getValidDateOrNull(this._dateAdapter.deserialize(control.value));
    return !controlValue || this._matchesFilter(controlValue)
      ? null
      : {'matDatepickerFilter': true};
  };

  private _matchesFilter(value: Date | null): boolean {
    const filter = this.datepickerFilter;
    return !filter || filter(value);
  }

  private _minValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
    const controlValue = this._dateAdapter.getValidDateOrNull(this._dateAdapter.deserialize(control.value));
    const min = this.minDate;
    return !min || !controlValue || this._dateAdapter.compareDate(min, controlValue) <= 0
      ? null : {'matDatepickerMin': {'min': min, 'actual': controlValue}};
  };

  private _maxValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
    const controlValue = this._dateAdapter.getValidDateOrNull(this._dateAdapter.deserialize(control.value));
    const max = this.maxDate;
    return !max || !controlValue || this._dateAdapter.compareDate(max, controlValue) >= 0
      ? null : {'matDatepickerMax': {'max': max, 'actual': controlValue}};
  };

  get formFieldClasses(): string[] {
    return [
      this.ngControl && this.ngControl.value?.length
        ? 'mat-mdc-form-field--filled'
        : 'mat-mdc-form-field--empty',
      this.readOnly ? 'mat-mdc-form-field--readonly' : '',
      this.ngControl &&
      this.ngControl.invalid &&
      (this.ngControl.touched || this.ngControl.dirty) &&
      !this.readOnly
        ? 'error-field'
        : '',
        (this.isActive && !this.errorText) || (this.ngControl && this.ngControl.value?.length)
        ? 'active-field'
        : ''
    ].filter(Boolean);
  }
}
